import { useMemo } from 'react';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import Slider from '@material-ui/core/Slider';
import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import { grey } from '@material-ui/core/colors';

import { PatientSPADIResult } from '../../../models/PatientForm';
import { InnerReportProps } from '../../../interfaces';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    paper: {
      padding: theme.spacing(1),
      marginTop: theme.spacing(1),
    },
    form: {
      margin: theme.spacing(2),
      textAlign: 'left',
    },
    referenceInfo: {
      fontSize: '0.75rem',
      color: grey[500],
    },
    formLabel: {
      width: '100%',
      textAlign: 'left',
    },
    slider: {
      textAlign: 'center',
      color: '#7A3FE1',
      width: '90%',
      marginLeft: '1rem',
    },
    sliderMakrLabel: {
      fontSize: '1rem',
    },
    sliderSubLabel: {
      whiteSpace: 'break-spaces',
      width: '50px',
      fontSize: '0.75rem',
      lineHeight: 1,
    },
    sectionElement: {
      height: '130px',
    },
  })
);

function SPADIInnerReport({ selectedForm }: InnerReportProps) {
  const classes = useStyles();

  const { answers, updated_at, disability, pain, total } = useMemo(() => {
    const { answers, results, updated_at } = selectedForm;
    const { disability, pain, total } = results as PatientSPADIResult;
    return {
      answers,
      updated_at,
      disability: disability.percentage,
      pain: pain.percentage,
      total: total.percentage,
    };
  }, [selectedForm]);

  const questions = [
    {
      title: 'Escala de incapacidade',
      subtitle:
        'Os nmeros ao lado de cada item representam o grau de dificuldade que voc teve ao fazer aquela atividade. O nmero zero representa "Sem dificuldade" e o nmero dez representa "No consegui fazer". Por favor, indique o nmero que melhor descreve quanta dificuldade voc teve para fazer cada uma das atividades na semana passada. Se voc no teve a oportunidade de fazer uma das atividades na semana passada, por favor, tente estimar qual nmero voc daria para sua dificuldade.',
      sections: [
        'Lavar seu cabelo com o brao afetado?',
        'Lavar suas costas como brao afetado?',
        'Vestir uma camiseta ou blusa pela cabea',
        'Vestir uma camisa que abotoa na frente?',
        'Vestir suas calas?',
        'Colocar algo em uma prateleira alta com o brao afetado?',
        'Carregar um objeto pesado de 5kg (saco grande de arroz) com o brao afetado?',
        'Retirar algo do bolso de trs com o brao afetado?',
      ],
      alternatives: [
        { label: 'N/A', value: -1 },
        {
          label: (
            <>
              <Typography>0</Typography>
              <Typography className={classes.sliderSubLabel}>
                Sem dificuldade
              </Typography>
            </>
          ),
          value: 0,
        },
        { label: '1', value: 1 },
        { label: '2', value: 2 },
        { label: '3', value: 3 },
        { label: '4', value: 4 },
        { label: '5', value: 5 },
        { label: '6', value: 6 },
        { label: '7', value: 7 },
        { label: '8', value: 8 },
        { label: '9', value: 9 },
        {
          label: (
            <>
              <Typography>10</Typography>
              <Typography className={classes.sliderSubLabel}>
                No consegui fazer
              </Typography>
            </>
          ),
          value: 10,
        },
      ],
    },
    {
      title: 'Escala de dor',
      subtitle:
        'Os nmeros ao lado de cada item representam quanta dor voc sente em cada situao. O nmero zero representa "Sem dor" e o nmero dez representa "A pior dor". Por favor, indique o nmero que melhor descreve quanta dor voc sentiu durante a semana passada em cada uma das seguintes situaes. Se voc no teve a oportunidade de fazer uma das atividades na semana passada, por favor, tente estimar qual nmero voc daria para sua dor.',
      sections: [
        'Qual foi a intensidade da sua dor quando foi a pior na semana passada?', //1
        'Quando se deitou em cima do brao afetado?', //2
        'Quando tentou pegfar algo em uma prateleira alta com o brao afetado?', //3
        'Quando tentou tocar a parte de trs do pescoo com o brao afetado?', //4
        'Quando tentou empurrar algo com o brao afetado?', //5
      ],
      alternatives: [
        { label: 'N/A', value: -1 },
        {
          label: (
            <>
              <Typography>0</Typography>
              <Typography className={classes.sliderSubLabel}>
                Sem dor
              </Typography>
            </>
          ),
          value: 0,
        },
        { label: '1', value: 1 },
        { label: '2', value: 2 },
        { label: '3', value: 3 },
        { label: '4', value: 4 },
        { label: '5', value: 5 },
        { label: '6', value: 6 },
        { label: '7', value: 7 },
        { label: '8', value: 8 },
        { label: '9', value: 9 },
        {
          label: (
            <>
              <Typography>10</Typography>
              <Typography className={classes.sliderSubLabel}>
                Pior dor
              </Typography>
            </>
          ),
          value: 10,
        },
      ],
    },
  ];

  const getRealIndex = (questionIndex: number, sectionIndex: number) => {
    return questions.reduce((acc, q, i) => {
      if (i < questionIndex) {
        return acc + q.sections.length;
      } else if (i === questionIndex) {
        return acc + sectionIndex;
      }
      return acc;
    }, 0);
  };

  return (
    <Grid container spacing={1}>
      <Grid item xs={12} lg={9}>
        <Paper classes={{ root: classes.paper }}>
          <Typography variant="h6">
            ndice de dor e incapacidade no ombro (SPADI)
          </Typography>
          <Typography variant="caption" display="block">
            {`Preenchido em: ${new Date(updated_at).toLocaleDateString(
              'pt-BR'
            )}`}
          </Typography>
          <Grid container spacing={1}>
            {questions.map((question, qIndex) => (
              <Grid item xs={12} lg={6} key={`question${qIndex}`}>
                <Grid container spacing={1}>
                  <Grid item xs={12}>
                    <Typography variant="subtitle1" align="left">
                      {`${qIndex + 1}. ${question.title}`}
                    </Typography>
                  </Grid>
                  {question.sections.map((section, sIndex) => (
                    <Grid
                      item
                      xs={12}
                      key={`question${qIndex}-section${sIndex}`}
                      className={classes.sectionElement}
                    >
                      <FormControl component="fieldset" fullWidth>
                        <FormLabel
                          component="legend"
                          className={classes.formLabel}
                        >
                          {section}
                        </FormLabel>
                        <Slider
                          defaultValue={answers[getRealIndex(qIndex, sIndex)]}
                          classes={{
                            root: classes.slider,
                            markLabel: classes.sliderMakrLabel,
                          }}
                          step={1}
                          valueLabelDisplay="auto"
                          marks={question.alternatives}
                          min={-1}
                          max={10}
                          disabled
                        />
                      </FormControl>
                    </Grid>
                  ))}
                </Grid>
              </Grid>
            ))}
          </Grid>
        </Paper>
      </Grid>
      <Grid item xs={12} lg={3}>
        <Paper classes={{ root: classes.paper }}>
          <Typography variant="h6">Resultado</Typography>
          <Grid container>
            <Grid item xs={12}>
              <Typography variant="subtitle1" align="left">
                {`Escala de Incapacidade: ${disability}`}
              </Typography>
            </Grid>
          </Grid>
          <Grid container>
            <Grid item xs={12}>
              <Typography variant="subtitle1" align="left">
                {`Escala de Dor: ${pain}`}
              </Typography>
            </Grid>
          </Grid>
          <Grid container>
            <Grid item xs={12}>
              <Typography variant="subtitle1" align="left">
                {`Escala total: ${total}`}
              </Typography>
            </Grid>
          </Grid>
        </Paper>
      </Grid>
    </Grid>
  );
}

export default SPADIInnerReport;
