import axios from 'axios';

const api = axios.create({
  baseURL: process.env.REACT_APP_BASE_URL,
});

api.interceptors.response.use(
  function (response) {
    // Qualquer cdigo de status que esteja dentro de 2xx far com que essa funo seja acionada
    // Faa algo com os dados de resposta
    return response;
  },
  function (error) {
    // Quaisquer cdigos de status que estejam fora de 2xx fazem com que esta funo seja acionada
    // Faa algo com erro de resposta
    if (error.response.status === 401) {
      localStorage.setItem('auth', JSON.stringify({}));
      const currentLocation = window.location;
      window.location.href = `${currentLocation.protocol}//${currentLocation.host}/login`;
    }
    return Promise.reject(error);
  }
);

export default api;
