import { useMemo } from 'react';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import LinearProgress from '@material-ui/core/LinearProgress';
import {
  makeStyles,
  createStyles,
  Theme,
  withStyles,
} from '@material-ui/core/styles';
import { deepOrange } from '@material-ui/core/colors';

import { PatientBasicResult } from '../../../models/PatientForm';
import { InnerReportProps } from '../../../interfaces';

const BorderLinearProgress = withStyles((theme: Theme) =>
  createStyles({
    root: {
      height: 10,
      borderRadius: 5,
    },
    colorPrimary: {
      backgroundColor:
        theme.palette.grey[theme.palette.type === 'light' ? 200 : 700],
    },
    bar: {
      borderRadius: 5,
      backgroundColor: deepOrange[500],
    },
  })
)(LinearProgress);

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    paper: {
      padding: theme.spacing(1),
      marginTop: theme.spacing(1),
    },
    oswLinearProgress: {
      margin: theme.spacing(1),
    },
    oswPercentage: {
      color: deepOrange[500],
    },
  })
);

function OswestryInnerReport({ selectedForm }: InnerReportProps) {
  const classes = useStyles();

  const { answers, updated_at, scoreOswestry, textOswestry } = useMemo(() => {
    const { answers, results, updated_at } = selectedForm;
    const { score, text } = results as PatientBasicResult;
    return {
      answers,
      updated_at,
      scoreOswestry: score || 0,
      textOswestry: text,
    };
  }, [selectedForm]);

  const questions = [
    {
      title: 'Seo 1 - Intensidade da dor',
      alternatives: [
        'Posso tolerar a dor que estou sentindo sem ter que  tomar analgsicos',
        'A dor  forte, mas suporto-a sem tomar analgsicos',
        'Os analgsicos aliviam completamente a dor',
        'Os analgsicos aliviam moderadamente a dor',
        'Os analgsicos aliviam muito pouco a dor',
        'Os analgsicos no afetam de forma alguma a dor e no os estou tomando',
      ],
    },
    {
      title: 'Seo 2 - Cuidados pessoais (lavar-se, vestir-se, etc)',
      alternatives: [
        'Posso me cuidar normalmente sem que isso cause mais dor',
        'Posso me cuidar normalmente, mas isso causa mais dor',
        'Di para eu me cuidar e eu sou lento e cuidadoso',
        'Preciso de alguma ajuda, mas consigo realizar a maioria dos meus cuidados pessoais',
        'Preciso de ajuda todos os dias para a maioria dos meus cuidados pessoais',
        'No consigo me vestir, me lavo com dificuldades e fico na cama',
      ],
    },
    {
      title: 'Seo 3 - Levantar pesos',
      alternatives: [
        'Posso levantar pesos considerveis sem sentir mais dor',
        'Posso levantar pesos considerveis, mas isso causa mais dor',
        'A dor me impede de levantar coisas pesadas, mas posso levant-las se bem posicionadas. Ex: Em cima de uma mesa',
        'A dor me impede de levantar pesos considerveis, mas posso levantar pesos leves a mdios, se estiverem posicionados convenientemente',
        'Posso levantar somente pesos bem leves',
        'No posso levantar ou carregar nada',
      ],
    },
    {
      title: 'Seo 4 - Caminhar',
      alternatives: [
        'A dor no me impede de andar qualquer distncia',
        'A dor me impede de andar mais de 1,6 quilmetros',
        'A dor me impede de andar mais de 800 metros',
        'A dor me impede de andar mais de 400 metros',
        'Posso andar somente com uma bengala ou muletas',
        'Fico na cama a maior parte do tempo e tenho que me arrastar para ir ao banheiro',
      ],
    },
    {
      title: 'Seo 5 - Sentar',
      alternatives: [
        'Posso me sentar em qualquer cadeira, por quanto tempo quiser',
        'S posso me sentar na minha cadeira favorita, por quanto tempo quiser',
        'A dor me impede de sentar por mais de 1 hora',
        'A dor me impede de sentar por mais de meia hora',
        'A dor me impede de sentar por mais de 10 minutos',
        'A dor me impede completamente de sentar',
      ],
    },
    {
      title: 'Seo 6 - Ficar em p',
      alternatives: [
        'Posso ficar em p o quanto tempo quiser, sem sentir mais dor',
        'Posso ficar em p o quanto tempo quiser, mas isso me causa mais dor',
        'A dor me impede de ficar em p por mais de 1 hora',
        'A dor me impede de ficar em p por mais de 30 minutos',
        'A dor me impede de ficar em p por mais de 10 minutos',
        'A dor me impede completamente de ficar em p',
      ],
    },
    {
      title: 'Seo 7 - Dormir',
      alternatives: [
        'A dor no me impede de dormir bem',
        'S posso dormir bem tomando comprimidos',
        'Mesmo quando tomo os comprimidos, s consigo dormir menos de seis horas',
        'Mesmo quando tomo os comprimidos, s consigo dormir menos de quatro horas',
        'Mesmo quando tomo os comprimidos, s consigo dormir menos de duas horas',
        'A dor me impede completamente de dormir',
      ],
    },
    {
      title: 'Seo 8 - Vida sexual',
      alternatives: [
        'Minha vida sexual  normal e no causa mais dor',
        'Minha vida sexual  normal, mas causa alguma dor adicional',
        'Minha vida sexual  quase normal, mas com muita dor',
        'Minha vida sexual  severamente restrita pela dor',
        'Minha vida sexual  quase inexistente devido  dor',
        'A dor me impede completamente de ter vida sexual',
      ],
    },
    {
      title: 'Seo 9 - Vida social',
      alternatives: [
        'Minha vida social  normal e no me causa mais dor',
        'Minha vida social  normal, mas aumenta o grau de dor',
        'A dor no tem efeito significativo na minha vida social, com exceo de limitar meus interesses energticos, como por exemplo, danar, etc.',
        'A dor restringiu minha vida social e no saio tanto como antes',
        'A dor restringiu minha vida social  minha casa',
        'No tenho vida social por causa da dor',
      ],
    },
    {
      title: 'Seo 10 - Viajar',
      alternatives: [
        'Posso viajar para qualquer lugar sem me causar mais dor',
        'Posso viajar para qualquer lugar, mas isso causa mais dor',
        'A dor  forte, mas consigo fazer jornadas de mais de duas horas',
        'A dor me restringe a jornadas de menos de 1 hora',
        'A dor me restringe a jornadas curtas necessrias, de menos de 30 minutos',
        'A dor me impede de viajar, exceto ir ao mdico ou ao hospital',
      ],
    },
  ];

  return (
    <Grid container spacing={1}>
      <Grid item xs={12} lg={9}>
        <Paper classes={{ root: classes.paper }}>
          <Typography variant="h6">Questionrio de Oswestry</Typography>
          <Typography variant="caption" display="block">
            {`Preenchido em: ${new Date(updated_at).toLocaleDateString(
              'pt-BR'
            )}`}
          </Typography>
          <Grid container spacing={2}>
            {questions.map((question, qIndex) => (
              <Grid item xs={6} key={`question${qIndex}`}>
                <Typography variant="body1" align="left">
                  {question.title}
                </Typography>
                <Typography variant="body1" align="left">
                  {question.alternatives.find(
                    (alt, altIndex) => answers[qIndex] === altIndex
                  )}
                </Typography>
              </Grid>
            ))}
          </Grid>
        </Paper>
      </Grid>
      <Grid item xs={12} lg={3}>
        <Paper classes={{ root: classes.paper }}>
          <Typography variant="h6">Resultado</Typography>
          <BorderLinearProgress
            variant="determinate"
            value={scoreOswestry}
            className={classes.oswLinearProgress}
          />
          <Typography
            variant="subtitle1"
            className={classes.oswPercentage}
          >{`${scoreOswestry}%`}</Typography>
          <Typography variant="subtitle1">{textOswestry}</Typography>
        </Paper>
        <Paper classes={{ root: classes.paper }}>
          <Typography variant="h6">Referncia</Typography>
          <Typography variant="body1" align="left">
            {'0% a 20% - Incapacidade mnima'}
          </Typography>
          <Typography variant="body1" align="left">
            {'21% a 40% - Incapacidade moderada'}
          </Typography>
          <Typography variant="body1" align="left">
            {'41% a 60% - Incapacidade intensa'}
          </Typography>
          <Typography variant="body1" align="left">
            {'61% a 80% - Aleijado'}
          </Typography>
          <Typography variant="body1" align="left">
            {'81% a 100% - Invlido'}
          </Typography>
        </Paper>
      </Grid>
    </Grid>
  );
}

export default OswestryInnerReport;
